/* ----------FONTS---------- */
@font-face {
    font-family: Roboto;
    src: local("Roboto-Regular"),
    url("../asset/fonts/Roboto-Regular.ttf") format("truetype");
}

@font-face {
    font-family: Futura;
    src: local("Futura-Medium-bt"),
    url("../asset/fonts/Futura-Medium-bt.ttf") format("truetype");
}





/* ----------CONTENT---------- */
.Filtered{
    background-image: url("../asset/seamless-fruit.jpg");
    height: 80vh;
    font-family: Futura;
    color: black;
}

.main-filter{
    background-image: linear-gradient(to bottom right, rgb(227, 54, 94), rgba(203, 89, 173, 0.942));
    padding: 50px;
    margin-top: 30px;
    border-radius: 30px;
}

.div-fruit{
    max-height: 250px;
    overflow-y: scroll;
    margin:5px;
    padding:5px;
    width: 100%;
    overflow: auto;
    background-color: white;
    opacity: 0.5;
}





/* ----------MEDIA ADJUST---------- */
@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}

    .main-filter {
        margin-left: 50px;
        margin-right: 50px;
    }
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}
}
  
