/* ----------FONTS---------- */
@font-face {
    font-family: Roboto;
    src: local("Roboto-Regular"),
    url("../asset/fonts/Roboto-Regular.ttf") format("truetype");
}

@font-face {
    font-family: Futura;
    src: local("Futura-Medium-bt"),
    url("../asset/fonts/Futura-Medium-bt.ttf") format("truetype");
}

@font-face {
    font-family: Arcade;
    src: local("Arcade"),
    url("../asset/fonts/Arcade.TTF") format("truetype");
}

@font-face {
    font-family: Digital;
    src: local("Digital"),
    url("../asset/fonts/Digital-Mono.ttf") format("truetype");
}

@font-face {
    font-family: Merch;
    src: local("Merch Copy"),
    url("../asset/fonts/Digital-Monospaced.ttf") format("truetype");
}





/* ----------CONTENT---------- */
.Stopwatch {
    height: 55vh;
    font-family: Futura;
    color: black;
    background-color:rgb(180, 183, 194);
}

.main-stopwatch {
    background-image: linear-gradient(to bottom right, rgb(237, 230, 231), rgba(230, 214, 225, 0.559));
    padding: 30px;
    margin-top: 30px;
    border-radius: 30px;
    width: 50%;
}

#stopwatch-tittle {
    margin-bottom: 20px;
    font-family: Futura;
    font-size: large;
}

.numbers {
    display: block;
    width: 100%;
    text-align: center;
    background-color: rgb(5, 7, 54);
    color: aquamarine;
    font-family: Digital;
    font-size: xx-large;
}

.buttons {
    margin-top: 10px;
}

.icon {
    height: 40px;
    padding: 10px;
}

.btn-stopwatch {
    background-color: black;
    margin: 10px;
    border-radius: 10px;
}





/* ----------MEDIA ADJUST---------- */
@media only screen and (min-width: 500px) {
    .Stopwatch{
        height: 80vh;
    }

    .numbers {
        width: 80%;
    }
} 

@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}

    .Stopwatch{
        height: 80vh;
    }

    #stopwatch-tittle {
        font-size: x-large;
    }

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}

    .main-stopwatch {
        padding: 35px;
    }

    .numbers {
        width: 80%;
    }
}
}
  
@media only screen and (min-width: 980px) {
    .numbers {
        width: 50%;
    }
} 